import React from 'react';
import '../wrap/scss/ConfirmModal.scss';
import { useDispatch, useSelector } from 'react-redux';
import { confirmModal } from '../reducer/confirmModal';

export default function ConfirmModalComponent()  {

    const dispatch = useDispatch();
    const selector = useSelector((state)=>state);


    const onClickCloseBtn=(e)=>{
        e.preventDefault();



        if(selector.confirmModal.confirmMsg==='  .'){  //  
            
            const value = {
                isConfirmModal: false,
                confirmMsg: '',
                join: true
            }

            dispatch(confirmModal(value));
            
        }
        else if(selector.confirmModal.confirmMsg==='  .'){  //   
            
            const value = {
                isConfirmModal: false,
                confirmMsg: '',
                join: false
            }
            
            dispatch(confirmModal(value));
    
            setTimeout(()=>{            // (ASYN )
                // router    useNavigate 
                window.location.pathname = '/sub6';
            },100)
            
        }
        else if(selector.confirmModal.confirmMsg==='   .'){  //   
            
            const value = {
                isConfirmModal: false,
                confirmMsg: '',
                join: false
            }
            
            dispatch(confirmModal(value));
    
            setTimeout(()=>{            // (ASYN )
                // router    useNavigate 
                window.location.pathname = '/sub7AdminSignIn';
            },100)
            
        }
        else{
            
            const value = {
                isConfirmModal: false,
                confirmMsg: '',
                join: false
            }

            dispatch(confirmModal(value));
        }

        const htmlEl = document.getElementsByTagName('html')[0];
        htmlEl.classList.remove('on');
    }

    return (
        <div id='confirmModal'>
            <div className='container'>
                <div className='confirm-box'>
                    <ul>
                        <li>
                            <div className='message-box'>
                                {
                                    // \n   
                                    selector.confirmModal.confirmMsg.split('\n').map((item)=>{
                                        return (
                                            <p>{item}</p>
                                        )
                                    })
                                }
                            </div>                             
                        </li>
                        <li>
                            <div className='button-box'>
                                <button onClick={onClickCloseBtn}></button>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

