import React from 'react';
import '../wrap/scss/ServiceModal2.scss';
import { useDispatch } from 'react-redux';
import { confirmService2 } from '../reducer/confirmService2Modal';

export default function ServiceModal2Component()  {

    const dispatch = useDispatch();


    const onClickClose=(e)=>{
        e.preventDefault();
        dispatch(confirmService2(false));
        
        const htmlEl = document.getElementsByTagName('html')[0];
        htmlEl.classList.remove('on');
    }

    return (
        <div id='serviceModal'>
            <div className='container'>
                <div className='service-box'>
                    <ul>
                        <li>
                            <h1>  ()</h1>
                        </li>
                        <li>
                            <div className="message-box">
                                <table>
                                    <thead>
                                        <tr>
                                            <th> </th>
                                            <th> </th>
                                            <th> </th>
                                        </tr>
                                    </thead>
                                    <tbody></tbody>
                                </table>
                                <p className='caption'>
                                     ,     ,         , ,    6 (, ,  , )  . < br/>
                                < br/>
                                     APPLE      *        . < br/>
                                < br/>
                                      .      ,        .
                                < br/>
                                </p>
                            </div>
                        </li>
                        <li>
                            <div className='button-box'>
                                <button onClick={onClickClose}></button>
                            </div>
                        </li>
                    </ul>
                    
                </div>
                
            </div>
        </div>
    );
};

