import React from 'react';
import { useNavigate } from 'react-router-dom';

export default function Sub7NoticeLeftComponent(){

    const navigate = useNavigate();

    const onClickSub7=(e)=>{
        e.preventDefault();
        navigate('/sub7');
    }

    return (
        <div className="left">
            <div className="left-header">
                <h2></h2>
            </div>
            <div className="left-list">
                <ul>
                    <li onClick={onClickSub7}><a href="!#"></a></li>
                    <li><a href="!#"> </a></li>
                    <li><a href="!#">1:1 </a></li>
                    <li><a href="!#"> </a></li>
                </ul>
                <div className="help-me">
                    <p>
                        <a href="!#">
                            <strong>  ?</strong>
                            <span>1:1 </span>
                        </a>
                    </p>
                </div>
            </div>
        </div>
    );
};
